"use client"

import type React from "react"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { AdminBlog } from "@/components/admin-blog"
import { AdminGallery } from "@/components/admin-gallery"
import { Lock, Newspaper, ImageIcon } from "lucide-react"

const ADMIN_PASSWORD = "admin123"

export default function AdminPage() {
  const [isAuthenticated, setIsAuthenticated] = useState(false)
  const [password, setPassword] = useState("")
  const [error, setError] = useState("")
  const [activeTab, setActiveTab] = useState<"blog" | "gallery">("blog")

  const handleLogin = (e: React.FormEvent) => {
    e.preventDefault()
    if (password === ADMIN_PASSWORD) {
      setIsAuthenticated(true)
      setError("")
    } else {
      setError("Incorrect password. Please try again.")
    }
  }

  if (!isAuthenticated) {
    return (
      <main className="min-h-screen bg-[#F5F5F5] py-16">
        <div className="max-w-md mx-auto px-4">
          <div className="bg-white rounded-lg shadow-lg p-8">
            <div className="flex justify-center mb-6">
              <div className="bg-black p-4 rounded-full">
                <Lock className="h-8 w-8 text-[#FFD700]" />
              </div>
            </div>
            <h1 className="text-2xl font-bold text-black text-center mb-6">Admin Login</h1>

            <form onSubmit={handleLogin} className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="password" className="text-black font-medium">
                  Password
                </Label>
                <Input
                  id="password"
                  type="password"
                  required
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  className="bg-white border-black"
                  placeholder="Enter admin password"
                />
              </div>

              {error && <p className="text-red-600 text-sm">{error}</p>}

              <Button type="submit" className="w-full bg-[#FFD700] text-black hover:bg-[#E6C200] font-semibold">
                Login
              </Button>
            </form>

            <p className="text-gray-500 text-sm text-center mt-4">For demo: password is &quot;admin123&quot;</p>
          </div>
        </div>
      </main>
    )
  }

  return (
    <main className="min-h-screen bg-[#F5F5F5] py-8">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-between mb-8">
          <h1 className="text-3xl font-bold text-black">Admin Dashboard</h1>
          <Button
            variant="outline"
            onClick={() => setIsAuthenticated(false)}
            className="border-black text-black hover:bg-black hover:text-white"
          >
            Logout
          </Button>
        </div>

        {/* Tabs */}
        <div className="flex gap-2 mb-8">
          <Button
            onClick={() => setActiveTab("blog")}
            className={
              activeTab === "blog"
                ? "bg-[#FFD700] text-black hover:bg-[#E6C200]"
                : "bg-white text-black border border-black hover:bg-gray-100"
            }
          >
            <Newspaper className="h-4 w-4 mr-2" />
            Blog Posts
          </Button>
          <Button
            onClick={() => setActiveTab("gallery")}
            className={
              activeTab === "gallery"
                ? "bg-[#FFD700] text-black hover:bg-[#E6C200]"
                : "bg-white text-black border border-black hover:bg-gray-100"
            }
          >
            <ImageIcon className="h-4 w-4 mr-2" />
            Gallery
          </Button>
        </div>

        {/* Content */}
        {activeTab === "blog" ? <AdminBlog /> : <AdminGallery />}
      </div>
    </main>
  )
}
